.elements {
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 68.75vw;
  column-gap: 17px;
  row-gap: 20px;
}

@media screen and (max-width: 320px) {
  .elements {
    flex-direction: row;
    column-gap: 0;
  }
}
